<template>
    <ion-page>
        <ion-header>
            <ion-toolbar>
                <ion-buttons slot="start">
                    <ion-back-button default-href="/order-list"></ion-back-button>
                </ion-buttons>
                <ion-title>訂單資訊</ion-title>
            </ion-toolbar>
        </ion-header>
        <ion-content class="order">
            <template v-if="order">
                <ion-grid>
                    <ion-row>
                        <ion-col class="ion-text-right" size="4"><ion-text>訂單編號 :</ion-text></ion-col>
                        <ion-col size="8"><ion-text>{{ order.order_no }}</ion-text></ion-col>
                    </ion-row>
                    <ion-row>
                        <ion-col class="ion-text-right" size="4"><ion-text>宮廟 :</ion-text></ion-col>
                        <ion-col size="8"><ion-text>{{ order.temple.title }}</ion-text></ion-col>
                    </ion-row>
                    <ion-row v-if="order.discount > 0">
                        <ion-col class="ion-text-right" size="4"><ion-text>訂單金額 :</ion-text></ion-col>
                        <ion-col size="8"><ion-text>{{ order.net_amount }}</ion-text></ion-col>
                    </ion-row>
                    <ion-row v-if="order.discount > 0">
                        <ion-col class="ion-text-right" size="4"><ion-text>折扣 :</ion-text></ion-col>
                        <ion-col size="8"><ion-text>{{ order.discount }}</ion-text></ion-col>
                    </ion-row>
                    <ion-row>
                        <ion-col class="ion-text-right" size="4"><ion-text>應付金額 :</ion-text></ion-col>
                        <ion-col size="8"><ion-text>{{ order.amount }}</ion-text></ion-col>
                    </ion-row>
                    <ion-row v-if="order.amount > 0">
                        <ion-col class="ion-text-right" size="4"><ion-text>付款方式 :</ion-text></ion-col>
                        <ion-col size="8"><ion-text>{{ order.payment_method.title }}</ion-text></ion-col>
                    </ion-row>
                    <ion-row v-if="order.payment">
                        <ion-col class="ion-text-right" size="4"><ion-text>付款資訊 :</ion-text></ion-col>
                        <ion-col size="8"><ion-text>{{ order.payment }}</ion-text></ion-col>
                    </ion-row>
                    <ion-row v-if="order.amount > 0 && order.pay_time">
                        <ion-col class="ion-text-right" size="4"><ion-text>付款時間 :</ion-text></ion-col>
                        <ion-col size="8"><ion-text>{{ order.pay_time }}</ion-text></ion-col>
                    </ion-row>
                    <ion-row v-if="order.drawback_time">
                        <ion-col class="ion-text-right" size="4"><ion-text>退款時間 :</ion-text></ion-col>
                        <ion-col size="8"><ion-text>{{ order.drawback_time }}</ion-text></ion-col>
                    </ion-row>
                    <ion-row v-if="order.cancel_time">
                        <ion-col class="ion-text-right" size="4"><ion-text>取消時間 :</ion-text></ion-col>
                        <ion-col size="8"><ion-text>{{ order.cancel_time }}</ion-text></ion-col>
                    </ion-row>
                    <ion-row v-if="order.create_time">
                        <ion-col class="ion-text-right" size="4"><ion-text>建立時間 :</ion-text></ion-col>
                        <ion-col size="8"><ion-text>{{ order.create_time }}</ion-text></ion-col>
                    </ion-row>
                    <ion-row>
                        <ion-col class="ion-text-right" size="4"><ion-text>訂單狀態 :</ion-text></ion-col>
                        <ion-col size="8"><ion-text>{{ order.statusDesc }}</ion-text></ion-col>
                    </ion-row>
                </ion-grid>
                <ion-grid>
                    <ion-col size="12" style="font-size:1.3rem;">
                        點燈列表
                    </ion-col>
                    <ion-row class="record ion-margin-top" :key="item.id" v-for="item in order.items">
                        <ion-col size="7">
                            <ion-row>
                                <ion-col>
                                    {{ item.light.title }}
                                </ion-col>
                                <ion-col>
                                    {{ item.name }}
                                </ion-col>
                            </ion-row>
                        </ion-col>
                        <ion-col class="ion-align-self-center" size="5">
                            {{ item.birthday }}
                        </ion-col>
                    </ion-row>
                </ion-grid>
                <ion-grid v-if="order.status == 1 && !order.payment">
                    <ion-row>
                        <ion-col>
                            <ion-button @click="pay" class="button-3">付款</ion-button>
                        </ion-col>
                    </ion-row>
                </ion-grid>
            </template>
        </ion-content>
        <ion-footer>
            <menu-bar></menu-bar>
        </ion-footer>
    </ion-page>
</template>

<script>
    import {
        executePayment,
        queryOrder
    } from "../api";

    import {
        IonBackButton,
        IonButton,
        IonButtons,
        IonCol,
        IonContent,
        IonFooter,
        IonGrid,
        IonHeader,
        IonPage,
        IonRow,
        IonText,
        IonTitle,
        IonToolbar
    } from "@ionic/vue";

    import {
        InAppBrowser
    } from "@awesome-cordova-plugins/in-app-browser";

    import MenuBar from "@/views/MenuBar.vue";

    export default {
        components: {
            IonBackButton,
            IonButton,
            IonButtons,
            IonCol,
            IonContent,
            IonFooter,
            IonGrid,
            IonHeader,
            IonPage,
            IonRow,
            IonText,
            IonTitle,
            IonToolbar,
            MenuBar
        },
        data() {
            return {
                id: this.$route.params.id,
                order: false
            };
        },
        methods: {
            pay() {
                executePayment(this.id, (result) => {
                    let browser = InAppBrowser.create(result.url, "_blank", {
                        hardwareback: "no",
                        hidenavigationbuttons: "yes",
                        location: "no",
                        usewkwebview: "yes"
                    });
                    browser.on("loadstop").subscribe((event) => {
                        if (event.url.startsWith(result.return_url)) {
                            browser.close();
                            queryOrder(this.id, (res) => {
                                if (res.order.status == 2) {
                                    this.$router.push({name: "OrderCompleted", params: {id: this.id}});
                                } else {
                                    this.order = res.order;
                                }
                            });
                        }
                    });
                });
            },
            query() {
                queryOrder(this.id, (result) => {
                    this.order = result.order;
                });
            }
        },
        mounted() {
            this.query();
        },
        watch: {
            $route(to) {
                if (to.name === "Order") {
                    this.id = to.params.id;
                    this.order = false;
                    this.query();
                }
            }
        }
    };
</script>

<style scoped>
    ion-grid {
        margin: 20px 20px 0 20px;
    }
    ion-col {
        color: #333;
        line-height: 36px;
    }
    .record {
        box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.2);
        padding: 0 10px;
    }
</style>
